@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;500;700;900&display=swap");
@import "/css/animate.css";
.mv-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 20vw;
  max-height: 240px;
  background: url(../img/sources/channel/mv.jpg) center center/cover no-repeat;
}

.mv-ttl {
  font-size: 2.25em;
}

.movie-block {
  padding: 84px 0 62px;
}
.movie-block .wrap {
  margin: 0 auto;
  width: 95%;
  max-width: 1140px;
}
.movie-block .block-title {
  margin-bottom: 34px;
}
.movie-block .block-title + .txt {
  margin-bottom: 40px;
  line-height: 1.75;
  text-align: center;
}
.movie-block .block-title + .txt a {
  display: inline-block;
  border-bottom: 1px solid;
  -webkit-transition: all 0.4s ease-in;
  transition: all 0.4s ease-in;
}
.movie-block .block-title + .txt a:hover {
  color: #00e0cd;
}
.movie-block .block-title + .txt a.blank {
  padding-right: 1.3em;
}
.movie-block .block-title + .txt a.blank::after {
  background-image: url(../img/sources/parts/icon-blank-blue.png);
  -webkit-transition: all 0.4s ease-in;
  transition: all 0.4s ease-in;
}
.movie-block .block-title + .txt a.blank:hover::after {
  background-image: url(../img/sources/parts/icon-blank-green.png);
}
.movie-block .item-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 72px;
}
.movie-block .item-list li {
  width: 31.5789473684%;
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
}
.movie-block .item-list li:not(:nth-child(3n)) {
  margin-right: 2.6315789474%;
}
.movie-block .item-list li:not(:nth-child(-n+3)) {
  margin-top: 44px;
}
.movie-block .item-list .txt-box {
  padding: 1.5em 5%;
  line-height: 1.6;
}
.movie-block .item-list .movie-ttl {
  margin-bottom: 1.5em;
  color: #10069f;
}
.movie-block .item-list .txt {
  color: #6d7777;
}
.movie-block .btn {
  text-align: center;
}
.movie-block .btn a {
  width: 88.2352941176%;
  max-width: 600px;
  color: #fff;
  border-color: #10069f;
  background: #10069f;
  -webkit-transition: all 0.4s ease-in;
  transition: all 0.4s ease-in;
}
.movie-block .btn a .blank::after {
  content: "";
  display: inline-block;
  margin-left: 0.9em;
  width: 0.8em;
  height: 0.8em;
  background: url(../img/sources/parts/icon-blank-white.png) center center/contain no-repeat;
  -webkit-transition: all 0.4s ease-in;
  transition: all 0.4s ease-in;
}
.movie-block .btn a:hover {
  color: #10069f;
  background: #fff;
}
.movie-block .btn a:hover::after {
  border-color: #10069f;
}
.movie-block .btn a:hover .blank::after {
  background-image: url(../img/sources/parts/icon-blank-blue.png);
}

.movie-box iframe {
  display: block;
  width: 100%;
  height: 240px;
}

@media screen and (max-width: 1200px) {
  .movie-box iframe {
    height: 20vw;
    min-height: 135px;
  }
}
@media screen and (max-width: 768px) {
  #container {
    font-size: 14px;
  }

  .movie-block .wrap {
    width: 84.375%;
  }
  .movie-block .item-list li {
    width: 48%;
  }
  .movie-block .item-list li:not(:nth-child(3n)) {
    margin-right: 0;
  }
  .movie-block .item-list li:nth-child(odd) {
    margin-right: 4%;
  }
  .movie-block .item-list li:not(:nth-child(-n+3)) {
    margin-top: 0;
  }
  .movie-block .item-list li:not(:nth-child(-n+2)) {
    margin-top: 40px;
  }

  .movie-box iframe {
    height: 26.953125vw;
    min-height: 135px;
  }
}
@media screen and (max-width: 640px) {
  .mv-block {
    height: 32vw;
  }

  .mv-ttl {
    font-size: calc(42/750*100vw);
  }

  .movie-block .wrap {
    width: 90.6666666667%;
  }
  .movie-block .block-title + .txt .pc {
    display: none;
  }
  .movie-block .item-list {
    margin: 0 auto 106px;
    width: 88.2352941176%;
  }
  .movie-block .item-list li {
    width: 100%;
  }
  .movie-block .item-list li:nth-child(odd) {
    margin-right: 0;
  }
  .movie-block .item-list li:not(:nth-child(-n+2)) {
    margin-top: 0;
  }
  .movie-block .item-list li:not(:first-child) {
    margin-top: 40px;
  }

  .movie-box iframe {
    height: 53.3333333333vw;
    min-height: 135px;
  }
}
@media screen and (max-width: 420px) {
  #container {
    font-size: 13px;
  }

  .mv-ttl {
    font-size: 20px;
  }

  .movie-block {
    padding: 50px 0 62px;
  }
  .movie-block .item-list {
    margin-bottom: 70px;
  }
}